@import url(https://fonts.googleapis.com/css?family=Merriweather&display=swap);
@import url(https://fonts.googleapis.com/css?family=Play&display=swap);
@import url(https://fonts.googleapis.com/css?family=Old+Standard+TT&display=swap);
@import url(https://fonts.googleapis.com/css?family=Playfair+Display:500i&display=swap);
@import url(https://fonts.googleapis.com/css?family=Six+Caps&display=swap);
@import url(https://fonts.googleapis.com/css?family=Krona+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=IM+Fell+English+SC);
@import url(https://fonts.googleapis.com/css?family=Permanent+Marker&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  -webkit-transition: 0.1s;
  transition: 0.1s;
}
/*Fonts*/
:root {
  --o-textcolor: #35302df8;
  --o-backcolor: #f3f2f2;
  --o-containercolor: #fff;
  --o-orange: #009a9c;
  --o-white: #53c7f0;
  --o-green: #bbb8b5;
  --o-navcolor: #53c7f0;
  --o-bordersolid: #f8ca00;
  --o-textsecondary: #202329;
  --o-cardtitle: #009a9c;
  --o-bordersolid-dark: #f3f2f2;
  --o-cardback: #fff;
}

.container {
  display: flex;
  flex-direction: row;
  height: 100%;
}
.blog-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 10px 1px 50px 30px;
  background-color: #fff;
  background-color: var(--o-containercolor);
  width: 100%;
  overflow-y: scroll;
  height: 100vh;
  box-shadow: -5px 10px 15px -6px rgba(0, 0, 0, 0.8);
}
.card-about {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  background-color: var(--o-cardback);
  margin: 60px 100px 5px;
  width: 90vh;
  height: auto;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  padding: 60px 5px 2px;
  border-radius: 3px;
}
.card-about-title {
  display: flex;
  flex-direction: row;
}
.card-about-context {
  display: flex;
  flex-direction: row;
}
.card-about-contact {
  display: flex;
  flex-direction: row;
  padding-left: 135px;
}
.card-about p {
  font-size: 60px;
  font-family: "Six Caps";
  color: #009a9c;
  color: var(--o-cardtitle);
  letter-spacing: 0.1rem;
  text-transform: uppercase;
  -webkit-writing-mode: vertical-lr;
      -ms-writing-mode: tb-lr;
          writing-mode: vertical-lr;
  -webkit-text-orientation: mixed;
          text-orientation: mixed;
  padding-left: 45px;
}
.card-about h1 {
  font-family: "Six Caps";
  font-size: 90px;
  text-transform: uppercase;
  margin-top: -25px;
  padding-left: 10px;
  color: #35302df8;
  color: var(--o-textcolor);
}
.card-about i {
  font-size: 14px;
  text-decoration: none;
  font-family: "Merriweather", serif;
  color: #35302df8;
  color: var(--o-textcolor);
  letter-spacing: 0.02rem;
}
.card-about a {
  font-size: 15px;
  text-decoration: none;
  color: #35302df8;
  color: var(--o-textcolor);
  letter-spacing: 0.01rem;
  padding-top: 15px;
}
.card-about img {
  width: 50%;
  height: 60%;
  border: 2px solid #009a9c;
  border: 2px solid var(--o-cardtitle);
  margin-left: 11px;
  margin-right: 11px;
}
.card-about button {
  margin-top: 35px;
  padding: 10px;
  background-color: #009a9c;
  background-color: var(--o-cardtitle);
  border: solid #009a9c;
  border: solid var(--o-cardtitle);
  color: white;
  box-shadow: -1px 10px 15px -6px rgba(0, 0, 0, 0.55);
  margin-bottom: 15px;
}
.card-about span {
  border-bottom: solid 1px #53c7f0;
  border-bottom: solid 1px var(--o-white);
  margin-left: 5px;
}

.card-about h1:hover {
  font-size: 100px;
}

.card-about a:hover {
  border-left: 25px solid #009a9c;
  border-left: 25px solid var(--o-cardtitle);
  padding: 8px;
  font-size: 25px;
  -webkit-transition: 0.75s;
  transition: 0.75s;
}
.card-about button:hover {
  box-shadow: -1px 10px 15px -6px #009a9c;
  box-shadow: -1px 10px 15px -6px var(--o-orange);
  padding: 8px;
  font-size: 16px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.card-records {
  display: flex;
  flex-wrap: wrap;
  width: 60vh;
  height: auto;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  padding: 40px 25px 10px;
  border-radius: 3px;
  margin: 80px 20px 10px;
  background-color: #202329;
  background-color: var(--o-textsecondary);
  border-radius: 5px;
}

.card-records a {
  font-size: 15px;
  text-decoration: none;
  font-family: "Playfair", serif;
  float: left;
  color: #fff;
  color: var(--o-containercolor);
  padding: 10px 15px 10px;
  margin-top: -95px;
  text-transform: uppercase;
}
.card-records p {
  font-size: 12px;
  text-decoration: none;
  font-family: "Playfair", serif;
  float: left;
  color: #bbb8b5;
  color: var(--o-green);
  padding: 10px 15px 10px;
  margin-top: -65px;
}
.card-records i {
  font-size: 14px;
  float: right;
  color: #bbb8b5;
  color: var(--o-green);
  padding: 10px 15px 10px;
  margin-top: -5px;
  margin-left: 430px;
}
.player {
  position: relative;
  bottom: 105px;
  box-shadow: -1px 13px 21px -6px rgba(0, 0, 0, 0.55);
}

.card {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  background-color: var(--o-cardback);
  margin: 30px 40px 20px;
  width: auto;
  height: auto;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  padding: 55px 85px 10px;
  /*  background-image: url("https://images.unsplash.com/photo-1493606278519-11aa9f86e40a?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1000&q=80");
    background-repeat: repeat-x;*/
}
.card-content {
  display: flex;
  flex-direction: row;
}
.card-readme {
  display: flex;
  flex-direction: column;
  padding: 20px;
}
.card-readme h3 {
  font-size: 15px;
  text-decoration: none;
  font-family: "PT Sans", sans-serif;
  color: #35302df8;
  color: var(--o-textcolor);
}
.card:hover {
  box-shadow: 0 10px 16px 0 #bbb8b5;
  box-shadow: 0 10px 16px 0 var(--o-green);
}
.card a {
  font-family: "Playfair Display";
  font-size: 55px;
  color: #202329;
  color: var(--o-textsecondary);
  padding: 30px;
  text-transform: lowercase;
  border-bottom: solid 2px #009a9c;
  border-bottom: solid 2px var(--o-orange);
  border-top: solid 2px #009a9c;
  border-top: solid 2px var(--o-orange);
}
.card p {
  font-size: 15px;
  text-decoration: none;
  font-family: "PT Sans", sans-serif;
  color: #202329;
  color: var(--o-textsecondary);
  line-height: 28px;
  width: auto;
  padding-top: 5px;
}
.card h1 {
  font-size: 20px;
  text-decoration: none;
  font-family: "Merriweather", serif;
  text-transform: uppercase;
  margin-bottom: 20px;
  color: #35302df8;
  color: var(--o-textcolor);
}
.card span {
  color: #009a9c;
  color: var(--o-orange);
  border-bottom: solid 1px;
  font-weight: bold;
}

.card img {
  width: auto;
  height: 400px;
  text-align: center;
  padding: 10px;
  border-radius: 20px;
}
.card button {
  padding: 15px;
  align-self: flex-end;
  background-color: #35302df8;
  background-color: var(--o-textcolor);
  border: solid 1px #35302df8;
  border: solid 1px var(--o-textcolor);
  text-transform: uppercase;
  color: white;
  font-size: 15px;
  box-shadow: 2px 2px 1px 0.1px white;
  margin-bottom: 35px;
}
.card button:hover {
  box-shadow: 0 4px 4px 0 #35302df8;
  box-shadow: 0 4px 4px 0 var(--o-textcolor);
}
navbar-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 800px;
  background-color: #53c7f0;
  background-color: var(--o-navcolor);
}
.navbar-container ul {
  display: block;
  color: #009a9c;
  color: var(--o-cardtitle);
  padding: 20px;
  text-decoration: none;
  font-size: 15px;
  text-align: right;
  font-family: "Krona One";
  letter-spacing: 0.1rem;
}
.navbar-container ul:hover {
  border-right: 6px solid #53c7f0;
  border-right: 6px solid var(--o-navcolor);
  font-weight: bold;
}
.navbar-container ul.active {
  font-size: 29px;
  font-family: "Krona One";
  margin-top: 120px;
  width: 450px;
  color: #35302df8;
  color: var(--o-textcolor);
  background-image: url();
}
.navbar-container p {
  text-decoration: none;
  font-size: 14px;
  text-align: right;
  font-family: "PT Sans", sans-serif;
  color: #009a9c;
  color: var(--o-orange);
}
.navbar-container ul.finish {
  text-decoration: none;
}
.navlink {
  background-color: transparent;
  text-decoration: none;
  color: #202329;
  color: var(--o-textsecondary);
}
.navbar-btn {
  display: flex;
  float: right;
}
.navbar-btn button {
  background-color: #009a9c;
  background-color: var(--o-orange);
  border: 1px solid transparent;
  padding: 2px;
  height: 100vh;
}
.navbar-btn button:hover {
  background-color: #202329;
  background-color: var(--o-textsecondary);
}
.welcome-container {
  padding: 490px;
  background-color: white;
}
.welcome-container p {
  border-right: solid 3px #009a9c;
  border-right: solid 3px var(--o-orange);
  white-space: nowrap;
  overflow: hidden;
  font-family: "Source Code Pro", monospace;
  font-size: 22px;
  color: #202329;
  color: var(--o-textsecondary);
  margin: 0 auto;
  margin-top: -100px;
}

/* Animation */
.welcome-container p {
  -webkit-animation: animated-text 3s steps(29, end) 1s 1 normal both,
    animated-cursor 600ms steps(29, end) infinite;
          animation: animated-text 3s steps(29, end) 1s 1 normal both,
    animated-cursor 600ms steps(29, end) infinite;
}

/* text animation */

@-webkit-keyframes animated-text {
  from {
    width: 0;
  }
  to {
    width: 480px;
  }
}

@keyframes animated-text {
  from {
    width: 0;
  }
  to {
    width: 480px;
  }
}

/* cursor animations */

@-webkit-keyframes animated-cursor {
  from {
    border-right-color: #009a9c;
    border-right-color: var(--o-orange);
  }
  to {
    border-right-color: transparent;
  }
}

@keyframes animated-cursor {
  from {
    border-right-color: #009a9c;
    border-right-color: var(--o-orange);
  }
  to {
    border-right-color: transparent;
  }
}

@media screen and (max-width: 800px) {
  .container {
    height: 100vh;
    width: auto;
  }
  .blog-container {
    padding: 1px 1px 1px;
  }
  .navbar-container {
    display: block;
    max-width: 100%;
    margin: 135px 70px 10px;
  }
  .navbar-container ul.active {
    font-size: 22px;
    width: 100%;
    text-align: center;
  }
  .navbar-container ul {
    font-size: 14px;
    font-family: "Permanent Marker";
    width: 100%;
    height: auto;
    text-align: center;
    letter-spacing: 0.1rem;
  }
  .navbar-container p {
    font-size: 11px;
  }

  .welcome-container {
    padding: 230px 40px 10px;
    background-color: white;
  }
  .welcome-container p {
    font-size: 15px;
    margin: 0 auto;
    margin-top: 100px;
  }
  .navbar-btn button {
    padding-left: 10px;
  }
  .navbar-btn button:hover {
    background-color: #f3f2f2;
    background-color: var(--o-backcolor);
  }
  .card {
    padding: 10px 30px 5px;
    margin-left: 7px;
    margin-right: 9px;
  }
  .card a {
    font-size: 14px;
    padding: 10px;
    text-transform: lowercase;
    border-bottom: solid 2px #009a9c;
    border-bottom: solid 2px var(--o-orange);
    border-top: solid 2px #009a9c;
    border-top: solid 2px var(--o-orange);
  }
  .card p {
    font-size: 8px;
    line-height: 15px;
  }
  .card h1 {
    font-size: 12px;
    text-decoration: none;
    font-family: "Merriweather", serif;
    text-transform: uppercase;
  }
  .card span {
    color: #009a9c;
    color: var(--o-orange);
    border-bottom: solid 1px;
    font-weight: bold;
  }

  .card img {
    width: 250px;
    height: 400px;
    text-align: center;
    border-radius: 23px;
    margin-left: 25px;
  }
  .card button {
    padding: 12px;
    margin-bottom: 15px;
    font-size: 10px;
    float: right;
  }
  .card button:hover {
    box-shadow: 0 4px 4px 0 #009a9c;
    box-shadow: 0 4px 4px 0 var(--o-orange);
  }
  .card-content {
    display: flex;
    flex-direction: column;
    margin-top: 5px;
  }
  .card-readme {
    display: flex;
    flex-direction: column;
    padding: 5px;
  }
  .card-readme h3 {
    font-size: 15px;
    text-decoration: none;
    font-family: "PT Sans", sans-serif;
  }
  .card-about {
    margin: 6px 10px 5px;
    height: auto;
    -webkit-transition: 0.3s;
    transition: 0.3s;
    padding: 50px 5px 2px;
    border-radius: 3px;
  }
  .card-about-title {
    display: flex;
    flex-direction: row;
  }
  .card-about-context {
    display: flex;
    flex-direction: column;
  }
  .card-about-contact {
    display: flex;
    flex-direction: row;
    padding-left: 35px;
  }
  .card-about p {
    font-size: 20px;
    padding-left: 10px;
    margin-top: -12px;
  }
  .card-about h1 {
    font-family: "Six Caps";
    font-size: 50px;
    margin-top: -25px;
    padding-left: 10px;
    letter-spacing: 0.1rem;
  }
  .card-about i {
    font-size: 11px;
    padding: 15px 10px;
    text-indent: 28px;
  }
  .card-about a {
    font-size: 12px;
    padding-top: 15px;
  }
  .card-about img {
    width: 60%;
    height: 60%;
    border: 2px solid #009a9c;
    border: 2px solid var(--o-cardtitle);
    margin-left: 41px;
    margin-right: 11px;
  }
  .card-about button {
    margin-top: 25px;
    padding: 10px;
    margin-bottom: 15px;
  }
  .card-about span {
    margin-left: 5px;
  }
  .card-records {
    width: 52vh;
    height: auto;
    margin: 100px 10px 40px;
    padding: 35px 15px 5px;
  }

  .card-records a {
    font-size: 15px;
  }
  .card-records p {
    font-size: 14px;
    text-indent: 15px;
  }
  .card-records i {
    font-size: 14px;
    float: right;
    color: #bbb8b5;
    color: var(--o-green);
    margin-left: 243px;
  }
}

